

.presale {
	max-width: 650px;
	border-radius: 15px;
	// background-color: #322a25;
	padding: 30px 22px;
	box-sizing: border-box;
	margin: 0 auto 24px;
	// color: #fff;
	.buy-btn {
		max-width: 288px;
		width: 288px;
		height: 67px;
		color: #fff;
		// font-family: Inter;
		font-style: normal;
		font-weight: 700;
		font-size: 18px;
		line-height: 22px;
		letter-spacing: .1em;
		text-transform: uppercase;
		box-shadow: 0 0 10px 0 #fb9c1a;
		margin-top: 50px;
	}
	.wallet-connect {
		max-width: 288px;
		width: 288px;
		height: 67px;
		color: #fff;
		// font-family: Inter;
		font-style: normal;
		font-weight: 700;
		font-size: 18px;
		line-height: 22px;
		letter-spacing: .1em;
		text-transform: uppercase;
		box-shadow: 0 0 10px 0 #fb9c1a;
		margin-top: 50px;
	}
}
.presale-title {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	flex-direction: row;
	-webkit-align-items: center;
	align-items: center;
	-webkit-justify-content: center;
	justify-content: center;
	margin-bottom: 14px;
	font-style: normal;
	font-weight: 400;
	font-size: 14px;
	line-height: 22px;
	>.logo {
		// display: flex;
    // justify-content: center;
		margin-right: 23px;
    height: 90px;
    width: 90px;
	}
}
.presale-info {
	max-width: 650px;
	border-radius: 15px;
	background-color: #322a25;
	padding: 21px 23px;
	box-sizing: border-box;
	margin: 0 auto 24px;
	color: #fff;
	.separator {
		width: 100%;
		height: 1px;
		margin: 20px 0;
		background: #fb9e1a;
	}
}
.presale-info-title {
	-webkit-align-items: center;
	align-items: center;
	-webkit-justify-content: start;
	justify-content: start;
	font-weight: 400;
	margin-bottom: 20px;
	font-size: 12px;
	line-height: 18px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	flex-direction: row;
	font-style: normal;
}
.presale-info-details {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	flex-direction: row;
	font-style: normal;
	// font-family: Inter;
	font-weight: 500;
	font-size: 9px;
	line-height: 11px;
	-webkit-justify-content: space-between;
	justify-content: space-between;
	.info {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		flex-direction: column;
		-webkit-align-items: flex-start;
		align-items: flex-start;
		p {
			margin-bottom: 0;
		}
	}
	p.sum {
		font-family: Carter,Open Sans,Helvetica Neue,sans-serif;
		font-size: 24px;
		line-height: 37px;
	}
	p.rate {
		margin-top: 10px;
	}
}
.presale-timer {
	-webkit-align-items: center;
	align-items: center;
	margin-bottom: 30px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	flex-direction: row;
	-webkit-justify-content: center;
	justify-content: center;
}
.presale-stages {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	flex-direction: row;
	-webkit-justify-content: center;
	justify-content: center;
}
div#stages {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	flex-direction: row;
	-webkit-justify-content: center;
	justify-content: center;
	width: 100%;
	padding: 0;
	-webkit-align-items: center;
	align-items: center;
	margin: 0;
	.stage {
		padding: 0;
		-webkit-align-items: center;
		align-items: center;
		margin: 0;
		display: inline-block;
		vertical-align: middle;
		height: 19px;
		background: rgba(180,182,190,.5);
		.money {
      display: flex;
      justify-content: center;
			top: -120%;
			font-weight: 700;
			font-size: 9px;
			line-height: 22px;
			position: relative;
			right: -50%;
			margin: 0;
			padding: 0;
			// font-family: Inter;
			font-style: normal;
		}
		.title {
      display: flex;
      justify-content: center;
			position: relative;
			right: -50%;
			margin: 0;
			padding: 0;
			// font-family: Inter;
			font-style: normal;
			bottom: -60%;
			font-weight: 500;
			font-size: 10px;
			line-height: 17px;
			text-transform: uppercase;
		}
		p {
			>span {
				color: #f1b806;
			}
		}
	}
	.separator {
		width: 1px;
		height: 22px;
		background-color: #fff;
	}
	.stage.filled {
		border-radius: 15px 0 0 15px;
		box-shadow: 0 0 10px 0 #fb9c1a;
		background: #f1b806;
	}
	.stage.filled2 {
		box-shadow: 0 0 10px 0 #fb9c1a;
		background: #f1b806;
	}
	.stage.last {
		border-radius: 0 15px 15px 0;
	}
}
#countdown {
	list-style: none;
	padding: 0;
	margin: 0;
	display: block;
	text-align: center;
	.countdown__item {
		display: inline-block;
		vertical-align: top;
		>* {
			display: inline-block;
			vertical-align: top;
		}
		span {
			font-style: normal;
			font-weight: 400;
			font-size: 18px;
			line-height: 28px;
		}
		p {
			// font-family: Inter;
			font-style: normal;
			font-weight: 500;
			font-size: 8px;
			line-height: 10px;
			text-transform: uppercase;
		}
	}
	.seperator {
		font-size: 18px;
		line-height: 28px;
		vertical-align: top;
		margin: 0 22px 0 21px;
	}
}
.btn-max {
	color: #fff !important;
}
input {
	&::-webkit-inner-spin-button {
		-webkit-appearance: none;
		margin: 0;
	}
	&::-webkit-outer-spin-button {
		-webkit-appearance: none;
		margin: 0;
	}
}
input[type=number] {
	-moz-appearance: textfield;
}
@media (min-width: 576px) {
	.exchange {
		width: 422px;
	}
	.presale-title {
		>.logo {
			display: -webkit-flex;
			display: flex;
		}
		font-size: 28px;
		line-height: 43px;
		margin-bottom: 28px;
	}
	.presale-timer {
		margin-bottom: 60px;
	}
	#countdown {
		.countdown__item {
			span {
				font-size: 48px;
				line-height: 74px;
			}
			p {
				font-size: 18px;
				line-height: 22px;
			}
		}
		.seperator {
			font-size: 36px;
			line-height: 74px;
			margin: 0 18px 0 19px;
		}
	}
	.presale-stages {
		margin-bottom: 60px;
	}
	div#stages {
		margin: 0 22px;
		.stage {
			.money {
				font-size: 18px;
			}
			.title {
				font-size: 14px;
			}
			height: 30px;
		}
		.separator {
			width: 2px;
			height: 35px;
		}
	}
	.presale-info {
		padding: 50px 60px;
		.separator {
			height: 3px;
			margin: 40px 0;
		}
	}
	.presale-info-title {
		margin-bottom: 41px;
		font-size: 28px;
		line-height: 43px;
	}
	.presale {
		.wallet-connect {
			margin-bottom: 20px;
		}
	}
	.presale-info-details {
		font-size: 18px;
		line-height: 22px;
		p.sum {
			font-size: 48px;
			line-height: 74px;
		}
		p.rate {
			margin-top: 40px;
		}
	}
}
